import React, { useState } from 'react'
import { storedQuestions } from '../Data/Questions'

function AppForm({ getInputValues, addQuestion, question, isDarkMode }) {

  return (
    <div>
      <form onSubmit={addQuestion}>
        <div className="p-2 row justify-content-around align-items-center border-bottom">
          <div className="col-xl-3 col-lg-12">
            <h3 className='text-center'>Questions and Answers</h3>
          </div>
          <div className="col-xl-3 col-lg-6 p-2">
            <input autoComplete='false' value={question.question} onChange={getInputValues} className={isDarkMode?'form-control dark-mode' : "form-control"} name='question' type="text" placeholder='Question' />
          </div>
          <div className="col-xl-3 col-lg-6 p-2">
            <input value={question.answer} onChange={getInputValues} className={isDarkMode?'form-control dark-mode' : "form-control"} name='answer' type="text" placeholder='Answer' />
          </div>
          <div className="col-xl-3 col-lg-12">
            <button className={isDarkMode ? 'btn btn-dark w-100 my-2' : 'btn btn-outline-dark w-100 my-2'} type='submit'>Add Question</button>
          </div>
        </div>
      </form>
    </div>
  )
}

export default AppForm