import React, { useEffect, useState } from 'react'
import { storedQuestions } from '../Data/Questions'

function QuestionAccordion({ deleteQuestion, questions, isDarkMode }) {

    return (
        <div className="accordion" id="accordionExample">
            {questions.map((question, index) => {
                return <div key={index} className="accordion-item">
                    <h2 className="accordion-header" id={"headingOne" + index}>
                        <button className={index == 0 ? isDarkMode ? "accordion-button dark-mode" : "accordion-button" : isDarkMode ? "accordion-button collapsed dark-mode" : "accordion-button collapsed "} type="button" data-bs-toggle="collapse" data-bs-target={"#collapseOne" + index} aria-expanded="true" aria-controls={"collapseOne" + index}>
                            #{index + 1} {question.question}
                        </button>
                    </h2>
                    <div id={"collapseOne" + index} className={index == 0 ? isDarkMode ? "accordion-collapse collapse show dark-mode" : "accordion-collapse collapse show" : isDarkMode ? "accordion-collapse collapse dark-mode" : "accordion-collapse collapse"} aria-labelledby={"headingOne" + index} data-bs-parent="#accordionExample">
                        <div className="accordion-body pb-0">
                            {question.answer}
                        </div>
                        <div className="d-flex justify-content-end">
                            <button onClick={() => { deleteQuestion(question.id) }} className='btn btn-outline-danger m-3 '>Delete</button>
                        </div>
                    </div>
                </div>
            })}
        </div>
    )
}

export default QuestionAccordion