import AppForm from './Components/AppForm';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css'
import '../node_modules/bootstrap/dist/js/bootstrap.bundle.min.js'
import './App.css';
import { useEffect, useState } from 'react';
import QuestionAccordion from './Components/QuestionAccordion';
import { storedQuestions } from './Data/Questions.js'
import Swal from 'sweetalert2';
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import DarkModeToggle from "react-dark-mode-toggle";



function App() {
  const [questions, setQuestions] = useState(storedQuestions)
  const [question, setQuestion] = useState({ id: '', question: '', answer: '' })
  const [isDarkMode, setIsDarkMode] = useState(JSON.parse(localStorage.getItem("isDarkMode")) || false);


  // useEffect(() => {
  //   isDarkMode ? document.body.classList.add("dark-mode") : document.body.classList.remove("dark-mode")
  //   if (isDarkMode) {
  //     import('@sweetalert2/theme-dark/dark.min.css')
  //   } else {
  //     import('@sweetalert2/themes/default/default.min.css')
  //   }
  // }, [])


  useEffect(() => {

    // isDarkMode ? document.body.classList.add("dark-mode") : document.body.classList.remove("dark-mode")

    if (isDarkMode) {
      // import('@sweetalert2/theme-dark/dark.min.css')
      document.documentElement.style.setProperty('--dark-mode-bg-color', '#121212');
      document.documentElement.style.setProperty('--dark-mode-color', '#fff');
    } else {
      // import('@sweetalert2/themes/default/default.min.css')
      document.documentElement.style.setProperty('--dark-mode-bg-color', '#fff');
      document.documentElement.style.setProperty('--dark-mode-color', '#121212');

    }

    localStorage.setItem("isDarkMode", isDarkMode)

  }, [isDarkMode])





  function addQuestion(e) {
    e.preventDefault();

    if (question.question == '' || question.answer == '') {
      toast.error('Sorry U have to add valid data!', {
        position: "top-right",
        autoClose: 2000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: isDarkMode ? "dark" : "light"
      });
    } else {
      toast.success('Question added successfully', {
        position: "top-right",
        autoClose: 2000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: isDarkMode ? "dark" : "light"
      });
      questions.push({ ...question, id: Date.now() })
      setQuestions(questions)
      localStorage.setItem('questions', JSON.stringify(questions))
      setQuestion({ question: '', answer: '' })
    }
  }

  function getInputValues(e) {
    setQuestion({ ...question, [e.target.name]: e.target.value })
  }

  function deleteQuestion(id) {
    let index = questions.findIndex((q) => q.id == id)
    let newArr = [...questions];

    const swalWithBootstrapButtons = Swal.mixin({
      customClass: {
        confirmButton: 'btn btn-success m-2',
        cancelButton: 'btn btn-danger m-2'
      },
      buttonsStyling: false
    })
    swalWithBootstrapButtons.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      cancelButtonText: 'No, cancel!',
      reverseButtons: true
    }).then((result) => {
      if (result.isConfirmed) {
        newArr.splice(index, 1)
        swalWithBootstrapButtons.fire(
          'Deleted!',
          'Your question has been deleted.',
          'success'
        ).then((result) => {
          if (result.isConfirmed) {
            setQuestions(newArr)
            localStorage.setItem("questions", JSON.stringify(newArr))
          }
        })
      } else if (
        /* Read more about handling dismissals below */
        result.dismiss === Swal.DismissReason.cancel
      ) {
        swalWithBootstrapButtons.fire(
          'Cancelled',
          'Your imaginary file is safe :)',
          'error'
        )
      }
    })

  }

  return (

    <div className='p-2'>
      <DarkModeToggle
        className='float-end'
        onChange={setIsDarkMode}
        checked={isDarkMode}
        size={80}
      />
      <div className='clearfix'></div>
      <section className='container py-5'>

        <ToastContainer
          position="top-right"
          autoClose={2000}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
          theme={isDarkMode ? "dark" : "light"}
        />
        <AppForm isDarkMode={isDarkMode} getInputValues={getInputValues} addQuestion={addQuestion} question={question} />

        <div className="row">
          <div className='col-xl-9 offset-xl-3 my-3'>
            <QuestionAccordion isDarkMode={isDarkMode} questions={questions} setQuestions={setQuestions} deleteQuestion={deleteQuestion} />
          </div>
        </div>
      </section>
    </div>

  );
}

export default App;